.highlight {
  margin: 2em 0 2.8em;

  table, tbody {
    display: block;
    width: 100%
  }

  td {
    border: 1px solid #dee;
  }

  tr {
    display: flex;
    width: 100%;
  }

  .code {
    flex: 1 0 0px;
    margin-left: -1px;

    // firefox again
    display: block;
    max-width: calc(100% - 2em);
  }
}

pre {
  padding: 10px;
  margin: 0;
  line-height: 23px;
  white-space: pre;
  overflow-x: auto;
  word-break: inherit;
  word-wrap: inherit;
}

.highlight .gl {
  background: #fafafa;
  border-right: 1px solid #dddddd;
  color: #cccccc;
  user-select: none;
  padding: 0 5px;
}

p code, li code, td code {
  color: #666666;
  background: #fafafa;
  padding: 3px 6px;
  border-radius: 8px;
  border: 1px dotted #cccccc;
  margin: 0 4px;
  font-size: 90%;
  word-wrap: break-word;
}

a code {
  color: inherit;
  text-decoration: underline;
  margin: 0 -1px;
  padding: 3px 10px;
}
